module.exports = "/* Flex Card Container */\n.container {\n  display: flex;\n  display: -moz-flex;\n  display: -webkit-flex;\n  -ms-box-orient: horizontal;\n  justify-content: center;\n  padding: 0;\n  margin: 0;\n  list-style: none;\n  line-height: 0;\n  flex-wrap: wrap; }\napp-card {\n  background: rgba(99, 205, 218, 0.5);\n  border-radius: 10px;\n  border: 1.5px solid #ffffffa2;\n  margin-top: 20px;\n  width: calc(100% * (1/2));\n  margin: 20px 10px;\n  transition-duration: .5s; }\napp-card:hover {\n    box-shadow: 10px 10px 36px 0px rgba(0, 0, 0, 0.75);\n    transform: scale(1.02); }\n@media only screen and (min-width: 600px) {\n  app-card {\n    width: calc(100% * (1 / 3)); } }\n@media only screen and (min-width: 900px) {\n  app-card {\n    width: 25%; } }\n\n/*# sourceMappingURL=data:application/json;base64, */"